@mixin clearfix() {

    &:before,
    &:after {
        content: " ";
        display: table;
    }

    &:after {
        clear: both;
    }
}

@mixin transition ($args...) {
    -webkit-transition: $args;
    -moz-transition: $args;
    -ms-transition: $args;
    -o-transition: $args;
    transition: $args;
}

@mixin transform ($args...) {
    -webkit-transform: $args;
    -moz-transform: $args;
    -ms-transform: $args;
    -o-transform: $args;
    transform: $args;
}

@mixin rotate ($deg) {
    @include transform(rotate(#{$deg}deg));
}

@mixin get-fontawesome-icon($icon-code, $font-size: 14px) {
    content: $icon-code;
    font: normal normal normal calc($font-size / 1) FontAwesome;
}

// Media breakpoints defined in settings
$break-md: 1199px !default;
$break-sm: 992px !default;
$break-xs: 768px !default;
$break-mb: 576px !default;
$break-large: 1570px !default;

// Media query mixin
@mixin media($screen) {

    @if $screen ==lg {
        @media (min-width: $break-md ) {
            @content;
        }
    }

    @else if$screen ==md {
        @media (max-width: $break-md ) {
            @content;
        }
    }

    @else if $screen ==sm {
        @media (max-width: $break-sm ) {
            @content;
        }
    }

    @else if $screen ==xs {
        @media (max-width: $break-xs ) {
            @content;
        }
    }

    @else if $screen ==mb {
        @media(max-width: $break-mb) {
            @content;
        }
    }

    @else if $screen ==large {
        @media(min-width: $break-large) {
            @content;
        }
    }
}