$prefix: '.rise-blocks';
@import "../../../scss/helpers/placeholder";
@import "../../../scss/helpers/mixins";

#{$prefix}-news-1-wrapper {
    display: grid;
    grid-gap: 20px;
    grid-template-columns: 60% auto;
    backface-visibility: hidden;

    #{$prefix}-news-1-card-wrapper {
    	position: relative;
	    background-size: cover !important;
	    background-repeat: no-repeat !important;
	    background-position: center !important;
	    display: flex;
	    align-items: flex-end;

	    &:first-child {
	    	grid-column-start: 1;
		    grid-row-start: 1;
		    grid-row-end: 3;
	    }

	    #{$prefix}-news-1-card {
		    position: relative; 
		    line-height: 1;
		    #{$prefix}-news-1-image {
		    	position: relative;
    			display: block;
    			height: 100%;		    	
		    }

		    #{$prefix}-news-1-post-content {
			    flex: 0 0 100%;		   

				p {
					margin: 15px 0 0;
					line-height: 1.7;
					color: #fff;
				}
			}			
		}

	    #{$prefix}-news-1-body {
			position: relative;
			z-index: 99;
			#{$prefix}-news-1-post-title {
			    color: #fff;
			    margin: 15px 0;
			    a {
			    	color: #fff;
			    	text-decoration: none;
			    	font-weight: 700;
					text-decoration: none;
					font-style: 28px;
					line-height: 32px;
			    }
			}

			#{$prefix}-news-1-post-cat {				    
			    a {
			    	background: #0b0b0b;
				    padding: 6px 7px;
				    vertical-align: middle;
				    color: #fff;
				    text-decoration: none;
				    margin-right: 16px;
			    }
			}


			#{$prefix}-news-1-meta-wrapper {
			    flex: 0 0 100%;
			    display: flex;
    			align-items: center;
    			flex-wrap: wrap;
    			

				a {
					color: #fff;
				    text-decoration: none;
				    margin-right: 16px;
				}
				
				#{$prefix}-news-1-post-date.meta-content {
					display: flex;
    				align-items: center;
					i {
					    margin-right: 6px;
					}
				} 
			}
		}

		#{$prefix}-news-1-overlay {
			bottom: 0;
		    display: block;
		    width: 100%;
		    position: absolute;
		    z-index: 1;
		    background: linear-gradient(to bottom,rgba(0,0,0,.4) 20%,#000 90%);
		    opacity: .6;
		    height: 100%;
		    left: 0;
		}		
	}

	@include media(mb) {
		grid-template-columns: repeat(1,1fr);
	}
}